@import "../../App.css";
.collapse {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    text-align: end;
    margin: 0 50px;
}

.dropdown-item {
    color: white;
}

.nav-link::after {
    position: relative;
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background: rgb(255, 255, 255);
    transition: transform 0.3s ease-in-out;
    transform: scale(0);
}

.nav-link:hover::after {
    transform: scale(1);
}

.header {
    background-image: url("https://images.pexels.com/photos/479444/pexels-photo-479444.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    width: 100vw;
    height: 40vh;
    display: flex;
    align-items: center;
}

.header-image {
    border-radius: 50%;
    width: 200px;
    max-width: 200px;
    display: block;
    margin: 0 20px;
}

.header-title {
    font-size: 50px;
    font-family: var(--principal-font);
    font-weight: bold;
    margin: 100px 0 0 50px;
}

@media screen and (max-width: 600px) {
    .header {
        width: 100vw;
        height: 60vh;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .header-image {
        border-radius: 50%;
        width: 150px;
        max-width: 200px;
        display: block;
        margin: 20px;
    }
    .header-title {
        max-width: 400px;
        font-size: 50px;
        font-family: var(--principal-font);
        font-weight: bold;
        margin: 10px;
    }
}