@import "../../App.css";
body {
    background-image: url("https://images.pexels.com/photos/479444/pexels-photo-479444.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
}

.card {
    border-radius: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.card-subtitle {
    margin-left: 20px;
}

.personal-card {
    flex-direction: row;
    align-items: center;
}

.section-title {
    font-size: 50px;
    font-weight: bold;
    font-family: var(--principal-font);
}

.skills-container {
    background-color: var(--pricipal-color);
}

@media screen and (max-width: 600px) {
    .personal-card {
        flex-direction: column;
        align-items: flex-end;
        padding: 20px;
        text-align: end;
    }
    .profile {
        text-align: start;
    }
}